.case {
  text-align: left;
  text-transform: capitalize;
}

.declension-table {
  border: 1px solid rgba(138, 138, 138, 0.308);
  border-collapse: collapse;
  display: inline-block;
  vertical-align: top;
  margin: 1rem;
  font-weight: bold;
  border-radius: 9px;
  overflow: hidden;
}
.declension-table.decliner {
  border: 1px solid var(--accent-1);
}
.decliner .declension-table-title {
  position: relative;
  color: var(--background-main);
  z-index: 0;
}
.decliner .declension-table-title::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--accent-1);
  opacity: 0.8;
  z-index: -1;
}
.declension-table-title {
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: rgba(189, 189, 189, 0.438);
  padding: 0.5rem;
}

.declension-table td,
.declension-table th {
  border: 1px solid rgba(255, 255, 255, 0.308);
  border-right: none;
  border-left: none;
  padding: 0.25rem 0.5rem;
  vertical-align: top;
}
.declension-table.decliner .declination {
  letter-spacing: 0.05rem;
}

.singular,
.plural {
  text-align: center;
}

.case-column,
.singular-column,
.plural-column {
  width: 7rem;
}

.case-column {
  /* background: rgba(255, 150, 232, 0.301); */
}
.singular-column {
}
.nominative {
  color: var(--accent-1);
}
.genitive {
  color: var(--accent-2);
}
.dative {
  color: var(--accent-3);
}
.accusative {
  color: var(--accent-4);
}
.ablative {
  color: var(--accent-5);
}
.vocative {
  color: var(--accent-6);
}
.root-word {
  color: rgb(131, 131, 131);
}
.use-cases {
  color: rgb(148, 148, 148);
  display: block;
  margin: 0;
  width: 100%;
  text-transform: lowercase;
}
